import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { LightsSchedulePage } from '../lights-schedule/lights-schedule.page';
import { ToastController } from '@ionic/angular';
  import { from } from 'rxjs';

@Component({
  selector: 'app-lightsconfig',
  templateUrl: './lightsconfig.page.html',
  styleUrls: ['./lightsconfig.page.scss'],
})
export class LightsconfigPage implements OnInit {

  constructor( public modalController: ModalController, public toastController: ToastController) {

   }

  ngOnInit() {
  }

  async presentModal() {
    const modal = await this.modalController.create({
      component: LightsSchedulePage
    });

    await modal.present();

  }

  async showToast() {
    const toast = await this.toastController.create({
      message: 'Coming soon',
      duration: 2000
    });
    toast.present();
  }

}
